/* ###### 4.3 jQuery Steps ###### */

/***** DEFAULT STYLE WIZARD *****/

.wizard {
  border: 1px solid #{$border};
  background-color: rgb($white) ;

  > {
    .steps {
      padding: 20px;

      >ul {
        padding: 0;
        margin-block-end: 0;
        display: flex;

        li {
          float: none;
          display: block;
          width: auto;

          .current-info {
            display: none;
          }

          .title {
            margin-inline-start: 5px;
            white-space: nowrap;
            transition: all 0.2s ease-in-out;
          }

          +li {
            margin-inline-start: 5px;
          }
        }
      }

      a {
        color: #{$gray-900};
        font-weight: 500;
        font-size: 15px;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover,
        &:active {
          color: #{$gray-900};
          font-weight: 500;
          font-size: 15px;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .number,
        &:hover .number,
        &:active .number {
          flex-shrink: 0;
          font-weight: 700;
          color: #{$primary};
          display: block;
          text-align: center;
          line-height: 2;
          width: 30px;
          height: 30px;
          background-color: #{$primary-02};
          border-radius: 50%;
          transition: all 0.2s ease-in-out;
        }
      }

      .disabled {
        display: none;

        a {
          color: #{$gray-500};

          &:hover,
          &:active {
            color: #{$gray-500};
          }
        }
      }

      .current a {
        color: #{$primary};

        &:hover,
        &:active {
          color: #{$primary};
        }

        .title,
        &:hover .title,
        &:active .title {
          display: inline-block;
        }

        .number,
        &:hover .number,
        &:active .number {
          background-color: #{$primary} !important;
          color: rgb($white) !important;
        }
      }

      .done a {
        color: #{$secondary};

        &:hover,
        &:active {
          color: #{$secondary};
        }

        .title,
        &:hover .title,
        &:active .title {
          display: none;
        }

        .number,
        &:hover .number,
        &:active .number {
          background-color: #{$secondary} !important;
          color: rgb($white) !important;
        }
      }
    }

    .content {
      border-block-start: 1px solid #{$border};
      border-block-end: 1px solid #{$border};
      min-height: 150px;
      padding: 20px;

      > {
        .title {
          font-size: 18px;
          color: #{$gray-900};
          font-weight: 700;
          margin-block-end: 5px;
          display: none;

          &.current {
            display: block;
          }
        }

        .body {
          float: none;
          position: static;
          width: auto;
          height: auto;

          input.parsley-error {
            border-color: #{$danger};

            +ul {
              list-style: none !important;
            }
          }
        }
      }
    }

    .actions {
      padding: 20px;

      >ul {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;

        >li:last-child a {
          background-color: #{$secondary};
        }
      }

      a {
        display: block;
        background-color: #{$primary};
        padding: 9px 25px;
        line-height: 1.573;
        color: #{$fixed-black} ;
        border-radius: 3px;
        margin-inline-end: 10px;

        &:hover,
        &:active {
          display: block;
          background-color: #{$primary};
          padding: 9px 25px;
          line-height: 1.573;
          color: rgb($white) ;
        }
      }

      .disabled a {
        background-color: #{$gray-500};
        border-radius: 3px;

        &:hover,
        &:active {
          background-color: #{$gray-500};
        }
      }
    }
  }

  &.vertical> {
    .steps {
      padding: 20px;

      ul li {
        +li {
          margin-inline-start: 10px;
        }

        .title {
          display: none;
        }
      }

      .current a .title {
        display: inline-block;
      }
    }

    .content {
      margin: 0;
      padding: 20px;
    }

    .actions {
      padding: 20px;

      ul {
        float: none;
        margin: 0;
        padding: 0;
      }
    }
  }
}

@media (min-width: 768px) {
  .wizard>.steps {
    padding: 25px;
  }
}

@media (min-width: 992px) {
  .wizard>.steps {
    padding: 30px;
  }
}

@media (prefers-reduced-motion: reduce) {
  .wizard>.steps>ul li .title {
    transition: none;
  }
}

@media (min-width: 576px) {
  .wizard>.steps>ul li .title {
    display: none;
    margin-inline-start: 10px;
  }
}

@media (min-width: 768px) {
  .wizard>.steps>ul li .title {
    display: none;
  }
}

@media (min-width: 576px) {
  .wizard>.steps>ul li+li {
    margin-inline-start: 20px;
  }
}

@media (min-width: 992px) {
  .wizard>.steps>ul li+li {
    margin-inline-start: 30px;
  }
}

@media (min-width: 1200px) {
  .wizard>.steps a {
    justify-content: flex-start;

    &:hover,
    &:active {
      justify-content: flex-start;
    }
  }
}

@media (prefers-reduced-motion: reduce) {
  .wizard>.steps a {

    .number,
    &:hover .number,
    &:active .number {
      transition: none;
    }
  }
}

@media (min-width: 576px) {
  .wizard>.steps a {

    .number,
    &:hover .number,
    &:active .number {
      font-size: 18px;
      font-weight: 600;
      line-height: 2.1;
      width: 40px;
      height: 40px;
    }
  }
}

@media (min-width: 576px) {
  .wizard>.steps .disabled {
    display: block;
  }
}

@media (min-width: 768px) {
  .wizard>.steps .done a {

    .title,
    &:hover .title,
    &:active .title {
      display: inline-block;
    }
  }
}

@media (min-width: 768px) {
  .wizard>.content {
    padding: 25px;
  }
}

@media (min-width: 992px) {
  .wizard>.content {
    padding: 30px;
  }
}

@media (min-width: 768px) {
  .wizard>.actions {
    padding: 25px;
  }
}

@media (min-width: 992px) {
  .wizard>.actions {
