}

@media (min-width: 576px) {
  .wizard.vertical>.steps {
    float: $float-start;
    width: 20%;
  }
}

@media (min-width: 768px) {
  .wizard.vertical>.steps {
    width: 15%;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.steps {
    padding: 25px;
    width: 30%;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.steps ul {
    flex-direction: column;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.steps ul li+li {
    margin-block-start: 10px;
    margin-inline-start: 0;
  }
}

@media (min-width: 768px) {
  .wizard.vertical>.steps ul li+li {
    margin-block-start: 20px;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.steps ul li .title {
    display: block;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.steps a {
    justify-content: flex-start;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.steps .current a .title {
    display: none;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.steps .current a .title {
    display: inline-block;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.content {
    border-block-start-width: 0;
    border-block-end-width: 0;
    width: 80%;
    float: $float-end;
    border-inline-start: 1px solid #{$border};
  }
}

@media (min-width: 768px) {
  .wizard.vertical>.content {
    width: 85%;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.content {
    width: 70%;
    padding: 25px;
  }
}

@media (min-width: 576px) {
  .wizard.vertical>.actions {
    width: 80%;
    float: $float-end;
    border-inline-start: 1px solid #{$border};
  }
}

@media (min-width: 768px) {
  .wizard.vertical>.actions {
    width: 85%;
  }
}

@media (min-width: 992px) {
  .wizard.vertical>.actions {
    width: 70%;
    padding: 25px;
  }
}

/****** EQUAL COLUMN WIDTH STEP INDICATOR *****/

.step-equal-width>.steps>ul {
  display: flex;

  >li {
    flex: 1;
    width: auto;
    float: none;
    flex: 1;
    width: auto;
    float: none;
  }

  display: flex;
}

/****** EQUAL COLUMN WIDTH STEP INDICATOR *****/

/***** CUSTOM STYLES *****/

.wizard-style-1>.steps>ul {
  a {
    padding: 0;
    height: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 0;
      font-size: 18px;
      font-weight: bold;
      color: #{$default-color};
      background-color: rgb($background-gradient-color);
      border-radius: 0;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-inline-end: 20px;
      margin-inline-start: 20px;
    }
  }

  .current a {

    .number,
    &:hover .number,
    &:active .number {
      background-color: #452efa;
      color: rgb($white) ;
    }
  }

  .done a {

    .number,
    &:hover .number,
    &:active .number {
      background-color: #643ab0;
      color: rgb($white) ;
    }
  }
}

.wizard-style-2>.steps>ul {
  a {
    padding: 0;
    height: 50px;
    border-radius: 50px;

    &:hover,
    &:active {
      padding: 0;
      height: 50px;
      border-radius: 50px;
    }

    .number,
    &:hover .number,
    &:active .number {
      width: 50px;
      height: 100%;
      border: 2px solid #{$border};
      font-size: 18px;
      font-weight: bold;
      color: #{$default-color};
      background-color: rgb($white) ;
    }

    .title,
    &:hover .title,
    &:active .title {
      margin-inline-end: 20px;
    }
  }

  .current a {

    .number,
    &:hover .number,
    &:active .number {
      border-color: #{$primary};
      color: #{$primary};
    }
  }

  .done a {

    .number,
    &:hover .number,
    &:active .number {
      border-color: #{$purple};
      color: #{$purple};
    }
  }
}

@media (min-width: 992px) and (max-width:1200px){
  .wizard.vertical>.steps .current a .title {
    display: none;
  }
  .wizard > .steps > ul li .title {
    display: none;
  }
  .wizard.vertical > .steps ul li .title {
    display: none;
  }
}